<template>
  <div class="balanceQuery">
    <myHeader :isBack="true" title="账单详情"></myHeader>
    <div class="seat-head"></div>
    <div class="container">
      <img :src="billImg">
      <NoRecord v-if="billImg==''"></NoRecord>
    </div>
  </div>
</template>

<script>
  import myHeader from '@/components/Header'
  import {queryBillRecordById} from'@/api/finance'
  import NoRecord from '@/components/NoRecord'
  import  {getParam} from '@/utils/wxUtil'
  import { Indicator } from 'mint-ui'
  export default {
    name: 'BalanceQuery',
    data() {
      return {
        billImg: ''
      }
    },
    components: {
      NoRecord,
      myHeader
    },
    created() {
      this._queryBillRecordById();
      Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      });
    },
    methods: {
      _queryBillRecordById() {
        // const rcordId= getParam('rcordId').replace(/%3D/g,'=').trim();
        let rcordId = this.$route.params.recordId
        queryBillRecordById({rcordId:rcordId}).then((res) => {
          Indicator.close();
          if(res.status == 400 ){

          }else {
            this.billImg = res.data;
          }



        }).catch(err => {
          Indicator.close();
        });
      }
    },
  }
</script>

<style scoped lang="scss">
.container{

}
</style>




